import React, { Component } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { Translation } from "react-i18next";
import { menuConfig } from '../../../shared/constants/menuItems';
import MenuCompnent from '../menu/Menu';
import { Card, CardBody, Grommet } from 'grommet';

export type TopicProps = {
  id: any;
  imagelink: any;
  title: string;
  description: string;
  className: string;
};

const theme = {
  global: {
    font: {
      family: 'Prosto One',
    },
  },
  card: {
    container: {
      background: '#1a3a39',
      elevation: 'none',
    },
    footer: {
      pad: { horizontal: 'medium', vertical: 'small' },
    },
  },
  heading: {
    color: '#ADFF0037',
    level: {
      2: {
        medium: {
          size: "24px",
          height: "30px",
        }
      }
    }
  },
};

class PersonalAccounPage extends Component<RouteComponentProps>  {
  render(): JSX.Element {

    return (
      <div>
        <MenuCompnent items={menuConfig} />
        <Grommet theme={theme}>
          <Card elevation="large" width="medium">
            <CardBody height="small"></CardBody>
          </Card> </Grommet>
      </div>
    );
  }
}

export default PersonalAccounPage;